import StatefulComponent from "../StatefulComponent";
import { connect } from "react-redux";
import PurchaseLimits from "./PurchaseLimits";
import { toast } from "react-toastify";

class PurchaseForm extends StatefulComponent {
    constructor(props) {
        super(props);

        this.handleChange = this.handleChange.bind(this);
        this.buy = this.buy.bind(this);
        this.state = {
            investAmount: 0
        }
    }

    buy() {
        // if (Number(this.state.investAmount) < 100 || Number(this.state.investAmount) > 20000) {
        //     toast.info("Please confirm your invest amount", {
        //         position: "top-center",
        //         autoClose: 3000,
        //         hideProgressBar: true,
        //         closeOnClick: true,
        //         pauseOnHover: true,
        //         draggable: true,
        //         progress: undefined,
        //     });
        //     return;
        // }
        this.props.dispatch({type:"BUY_TOKEN", payload: {investTokenAmount: this.state.investAmount}});
    }

    handleChange(event) {
        var value = event.target.value;
        var pattern = /[^0-9.]/g;
        let result = value.match(pattern);
        if (!value || !result ) {
            this.setState({investAmount: event.target.value});
            this.props.dispatch({
                type: 'GET_TOKEN_AMOUNT', payload: {investTokenAmount: event.target.value}
              });
        }
    }

    render() {
        return (
            <>
                <div className="purchase-form wallet-connection">
                    {/* <PurchaseLimits/> */}
                    <div className="purchase-amount">
                        <form>
                            
                            <div className="newInputs">

                                <div className="leftInputs NewHolder">

                                <div className="amoutToken"><label>Enter amount</label></div>
                                    <div className="newInputsItem">
                                        <input type="text" placeholder="1000" value={this.state.investAmount} 
                                            onChange={this.handleChange}/>
                                        <button className="selectDinar">
                                            <div className="optionDinar" >
                                                <div className="imageDinar"><img alt="USDC.e" src="https://raw.githubusercontent.com/ava-labs/avalanche-bridge-resources/main/tokens/USDC/logo.png" /></div>
                                            </div>
                                            <div className="">{this.props.investToken}</div>
                                            {/* 
                                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" aria-hidden="true" width="18">
                                                <path fillRule="evenodd" d="M5.293 7.293a1 1 0 011.414 0L10 10.586l3.293-3.293a1 1 0 111.414 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414z" clipRule="evenodd"></path>
                                            </svg>
                                            */}
                                        </button>
                                    </div>
                                    <PurchaseLimits compact={true}/>
                                </div>

                                <div className="rightInputs NewHolder">

                                    <div className="amoutToken"><label>{this.props.returnToken} token</label></div>
                                    <div className="newInputsItem">
                                        {/* TODO: autofill token amount based on purchase amount */}
                                        <input className="disablePointer" type="text" placeholder="Autofill" 
                                            value={this.props.returnTokenAmount} onChange={()=>{}} readOnly/>
                                        <button className="selectDinar">
                                            <div className="optionDinar" >
                                                <div className="imageDinar"><img src="/img/swapsicle-coin.png" alt="Swapsicle token"/></div>
                                            </div>
                                            <div className="">POP</div>
                                        </button>
                                    </div>
                                    <div className="leftBottom">Token price: $0.20</div>
                                </div>

                            </div>

                            {/* <div className="selectCtry">
                                <label>
                                    Select country
                                    <select>
                                        <option>So many options</option>
                                        <option>...</option>
                                    </select>
                                </label>
                            </div> */}
                            <div className="checkBoxr">
                                <label><input type="checkbox" onChange={()=>{}}/> I have read the Terms &amp; Conditions</label>
                            </div>
                            <button className="connectWallet" onClick={this.buy}>Buy now</button>
                        </form>
                    </div>
                </div>
            </>
        );
    }
}

const mapStateToProps = state => {
    return {
        investToken: state.investToken,
        returnTokenAmount : state.returnTokenAmount,
        returnToken: state.returnToken
    };
}

const mapDispatchToProps = dispatch => {
    return {
        dispatch
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(PurchaseForm);
