import StatefulComponent from "../StatefulComponent";
import { connect } from "react-redux";
import PurchaseForm from "./PurchaseForm";
import {toast} from "react-toastify";
import PurchaseLimits from "./PurchaseLimits";
import PurchaseComplete from "./PurchaseComplete";
// mili popup
import Popup from './Popup/Popup';

class Purchase extends StatefulComponent {
  state = {
    isOpen: false,
  };
  constructor() {
    super();
    this.handleConnect = this.handleConnect.bind(this);
  }

  async handleConnect() {
    if (window.ethereum) {
      await window.ethereum.enable();
      this.props.dispatch({
        type: 'CONNECT_WALLET',
      });
    } else {
      toast.info('Please install metamask on your device', {
        position: 'top-center',
        autoClose: 3000,
        hideProgressBar: true,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    }
    this.togglePopup();
  }

  togglePopup = () => {
    this.setState({ isOpen: !this.state.isOpen });
  };

  static getDerivedStateFromProps(props, state) {
    return props;
  }
    render() {
        return (
            <>
                {
                    this.props.account ?
                        (this.props.transaction ?
                            <PurchaseComplete/>
                            : (this.props.purchaseAllowed ?
                                <PurchaseForm/>
                                : <div className="purchase-not-allowed wallet-connection">Your account is not allowed to purchase at this time.</div>))
                        :
                        <div className="wallet-connection selectWallet">
                            <PurchaseLimits/>
                            <div><span className="grayish">Sale information</span></div>
                            <div className="wallet-text">Public sale is open to all. NFT or Nest holders can only purchase once.</div>
                            <button onClick={this.togglePopup} className="connectWallet">Connect wallet to stake</button>
                            {this.state.isOpen && (
                                <Popup
                                    content={
                                    <>
                                        <div className="connectTitle">Connect a wallet</div>

                                        <div className="walletHolder">
                                            <div className="walletItem"><a onClick={this.handleConnect} href="#"><img alt="MetaMask" src="/img/MetaMask_Fox.png" />MetaMask<span className="arrowRightBtn"><i className="fa-solid fa-chevron-right"></i></span></a></div>
                                            {/* 
                                            <div className="walletItem"><a href="#">WalletConnect<span className="arrowRightBtn"><i claclassNamess="fa-solid fa-chevron-right"></i></span></a></div>
                                            <div className="walletItem"><a href="#">Keystone<span className="arrowRightBtn"><i className="fa-solid fa-chevron-right"></i></span></a></div>
                                            <div className="walletItem"><a href="#">Lattice<span className="arrowRightBtn"><i className="fa-solid fa-chevron-right"></i></span></a></div>
                                            <div className="walletItem"><a href="#">Coinbase wallet<span className="arrowRightBtn"><i className="fa-solid fa-chevron-right"></i></span></a></div>
                                            <div className="walletItem"><a href="#">Binance<span className="arrowRightBtn"><i className="fa-solid fa-chevron-right"></i></span></a></div>
                                            <div className="walletItem"><a href="#">Clover<span className="arrowRightBtn"><i className="fa-solid fa-chevron-right"></i></span></a></div>
                                            <div className="walletItem"><a href="#">Torus<span className="arrowRightBtn"><i className="fa-solid fa-chevron-right"></i></span></a></div>
                                                */}
                                        </div>
                                    </>
                                    }
                                    handleClose={this.togglePopup}
                                />
                                )}
                        </div>
                }
            </>
        );
    }
}

const mapStateToProps = state => {
    return {
        transaction : state.transaction,
        account : state.account,
        purchaseAllowed: state.purchaseAllowed
    };
}

const mapDispatchToProps = dispatch => {
    return {
        dispatch
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Purchase);
